import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { WebService } from '../web.service';

@Component({
  selector: 'app-novo-cadastro',
  templateUrl: './novo-cadastro.component.html',
  styleUrls: ['./novo-cadastro.component.css']
})
export class NovoCadastroComponent implements OnInit {
  novoUsuario = { nome: "", login: "", senha: "" }

  constructor(private web: WebService, private routes: Router) { }

  cadastrar() {
    this.web.cadastrarUsuario(this.novoUsuario).subscribe(res => {
      if (res.ok == true) {
        alert(res.body.msg);
        sessionStorage.setItem('token', res.body.token);
        sessionStorage.setItem('expires', res.body.expiry);
        sessionStorage.setItem('userName', res.body.userName);
        this.routes.navigate(['nova-mensagem']);
      } else {
        alert("Erro de Cadastro");
      }
    });
  }

  ngOnInit(): void {
  }

}
